<template>
  <v-container>
    <v-row>
      <v-col cols="6">
        <v-text-field v-model="keyword" label="Keyword"> </v-text-field>
      </v-col>
      <v-col cols="6">
        <v-btn @click="searchItem(keyword)">Load Items</v-btn>
      </v-col>
      <v-col cols="6">
        <ul>
          <li v-for="item in searchArr" :key="item.id">
            {{ item.name }}
            <v-btn small @click="parseStat(item)">Load Stats</v-btn>
            <ul>
              <li>{{ item.id }}</li>
              <li>{{ item.slots }}</li>
              <li>{{ item.level }}</li>
              <li>{{ item.desc }}</li>
            </ul>
          </li>
        </ul>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import itemsData from "../assets/data/items.json";
import itemDescData from "../assets/data/item_descriptions.json";
export default {
  name: "HelloWorld",
  data() {
    return {
      title: "Hello Title",
      items: itemsData,
      itemsList: Object.keys(itemsData),
      itemDesc: itemDescData,
      itemDescList: Object.keys(itemDescData),
      searchArr: [],
      currentItem: {},
      testItem: [],
      testsd: true,
      itemID: 23072,
      keyword: "Bihu Cannions +2",
      setStats: [],
    };
  },
  methods: {
    searchItem(keyword) {
      let regMatch = new RegExp(keyword.toLowerCase(), "g");
      this.searchArr = [];
      this.itemsList.forEach((key) => {
        let itemName = this.items[key].en.toLowerCase();
        if (itemName.match(regMatch)) {
          let item = {};
          item.id = key;
          item.name = this.items[key].en;
          item.category = this.items[key].category;
          item.level = this.items[key].level;
          item.slots = this.items[key].slots;
          item.desc = this.itemDesc[key].en;
          this.searchArr.push(item);
        }
      });
    },
    parseStat(item) {
      item.descTest = item.desc
      let gearItem = {}
      // Get Base Stats DMG/DEF/Delay
      gearItem.DEF = this.getStatDefense(item)
      gearItem.DMG = this.getStatDamage(item)
      gearItem.Delay = this.getStatDelay(item)
      gearItem.HP = this.getStatHP(item)
      gearItem.MP = this.getStatMP(item)
      gearItem.STR = this.getStatSTR(item)
      gearItem.DEX = this.getStatDEX(item)
      gearItem.VIT = this.getStatVIT(item)
      gearItem.AGI = this.getStatAGI(item)
      gearItem.INT = this.getStatINT(item)
      gearItem.MND = this.getStatMND(item)
      gearItem.CHR = this.getStatCHR(item)
      // Magic Accuracy/Evasion/Attack should go first to remove conflicts with normal(physical) Stats
      gearItem.mAcc = this.getStatMagicAcc(item)
      gearItem.pAcc = this.getStatMeleeAcc(item)
      
      item.stats = gearItem
      console.log(item)
    },
    getStatDefense(item) {
      let desc = item.descTest
      let tests = [/DEF(:-|:)\d*/g];
      let stat = 0;
      tests.forEach((test) => {
        let match = desc.match(test);
        if (match !== null) {
          let type = match[0].match(/(:|-)/g)[0];
          let value = match[0].match(/\d+/g)[0];
          if (type == "-") {
            stat = Number(value) * -1;
          } else {
            stat = Number(value) * 1;
          }
          desc = desc.replace(test,"")
          item.descTest = desc
        }
      });
      return stat;
    },
